.btn {
  padding: 10px 15px;
  font-size: 14px;
  border-radius: unset;
  cursor: pointer;
}

.btn-icn {
  width: 22px;
  height: 18px;
  margin-right: 10px;
  float: left;
}

.btn-icn svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.btn-primary {
  border: 1px solid var(--primary-color);
  background-color: var(--primary-color);
  color: var(--white-color);
}
.btn-primary .btn-icn svg {
  fill: var(--white-color);
}
.btn-primary:hover {
  background-color: var(--tertiary-color);
  border: 1px solid var(--tertiary-color);
}

.btn-outline-primary {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}
.btn-outline-primary .btn-icn svg {
  fill: var(--primary-color);
}
.btn-outline-primary:hover {
  border: 1px solid var(--primary-color);
  background-color: var(--primary-color);
  color: var(--white-color);
}
.btn-outline-primary:hover .btn-icn svg {
  fill: var(--white-color);
}
