.spec {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.spec-image {
  margin: 10px;
  width: 100%;
  max-height: 515px;
  background-color: rgba(0, 109, 182, 0.2);
  overflow: hidden;
}
.spec-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  position: relative;
  bottom: -30px;
}

.spec-items {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.spec-card {
  min-height: 215px;
  margin: 10px;
}

@media screen and (min-width: 568px) {
  .spec-image img {
    height: 300px;
  }

  .spec-items {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }

  .spec-card {
    width: 45%;
  }
}

@media screen and (min-width: 769px) {
  .spec {
    flex-direction: row;
    justify-content: space-between;
  }

  .spec-image {
    width: 40%;
    object-fit: cover;
    object-position: center;
  }

  .spec-image img {
    height: 100%;
  }

  .spec-items {
    width: 60%;
  }
}
