.qf {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.qf-image {
  width: 100%;
  margin-top: 20px;
  max-height: 515px;
  background-color: rgba(0, 109, 182, 0.2);
  overflow: hidden;
}
.qf-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  position: relative;
  bottom: -10px;
}

.qf-items {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.qf-card {
  min-height: 215px;
  margin: 10px;
}

@media screen and (min-width: 568px) {
  .qf-image {
    height: 100%;
  }

  .qf-items {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }

  .qf-card {
    width: 45%;
  }
}

@media screen and (min-width: 769px) {
  .qf {
    flex-direction: row;
    justify-content: space-between;
  }

  .qf-image {
    width: 40%;
    max-height: 500px;
  }

  .qf-items {
    width: 60%;
    margin-top: 15px;
  }
}
