.project-card {
  margin-bottom: 10px;
}

.project-card > img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

.project-foot {
  padding: 10px 5px;
}

.project-link li {
  display: inline-block;
  list-style-type: none;
  padding: 0 10px 0 0;
}

.project-link > li a {
  text-decoration: none;
  color: var(--text-dark);
  font-size: calc(10px + (16 - 10) * ((100vw - 300px) / (1600 - 300)));
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.link-detail-icn {
  width: 22px;
  height: 18px;
  margin-right: 8px;
}

.link-detail-icn svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  float: left;
}

@media screen and (min-width: 426px) {
  .project-foot {
    display: flex;
    justify-content: space-between;
  }

  .project-link li {
    display: inline-block;
    list-style-type: none;
  }
}

@media screen and (min-width: 769px) {
  .project-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .project-card {
    width: 50%;
    padding: 15px;
  }
}
