.tools {
  background-color: var(--grey-color);
  padding: 50px 0 70px 0;
}

.tools-item {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 100%;
  margin-top: 40px;
}

.tools-item ul {
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.tools-img {
  margin: 25px 15px;
  max-width: 100px;
  max-height: 40px;
}

.tools-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
